.header-top{
    height: 30vh;
    background-image: url("../../imgs/header.jpg");
    background-repeat: no-repeat;
    background-size: 100%;
    background-position: 50% 40%;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: center;
        justify-content: center;
    -ms-flex-align: center;
        align-items: center;
}

.header-title{
    font-size: 5vw;
}

.header-navbar{
    width: 100%;
    margin-top: 10px;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;
}
.navbar-line{
    border: 1px solid #000;
}

.navbar-content{
    display: -ms-flexbox;
    display: flex;
    -ms-flex-align: center;
        align-items: center;
    width: 100%;
}

.navbar-left{
    width: 60%;
    display: -ms-flexbox;
    display: flex;
}

.navbar-right {
    width: 40%;
    display: -ms-flexbox;
    display: flex;
}


.navbar-right{
    -ms-flex-pack: end;
        justify-content: flex-end;
    -ms-flex-align: center;
        align-items: center;
    margin-right: 10px;
}

.navbar-item{
    padding: 10px;
    margin-left: 10px;
    text-decoration: none;
    color: #000;
}

.navbar-item-text{
    padding: 0 10px !important;
    margin-left: 10px !important;
    text-align: center;
    vertical-align: center;
    border-right: 1px solid #000;
}

.navbar-item:hover{
    background-color: #EEEEEE;
    cursor: pointer;
}


@media only screen and (max-width: 850px) {

    .header-top{
        height: 15vh;
    }

    .show-on-large-screen {
        display: none;
    }
}


