.login-content{
    -ms-flex-positive: 1;
        flex-grow: 1;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: center;
        justify-content: center;
    -ms-flex-align: start;
        align-items: flex-start;
    padding: 40px 0;
}

.login-form{
    border: 2px solid #000;
    padding: 30px;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;
    width: 40%;
}

.form-item{
    margin: 20px 0;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;
    -ms-flex-pack: center;
        justify-content: center;
    -ms-flex-align: center;
        align-items: center;
}

.login-form input{
    padding: 10px 0;
    outline: none;
    border: none;
    border-bottom: 1px solid #000;
    width: 100%;
    font-size: 14pt;
    text-align: center;
}

.login-alert{
    width: 100%;
    text-align: center;
    color: red;
    min-height: 18px;
    padding: 5px 0 !important;
}

.login-form-btn{
    width: 50%;
    padding: 10px 0;
    border: none;
    border: 1px solid #000;
    font-size: 14pt;
    outline: none;
    background-color: transparent;
}

.login-form-btn:hover{
    background-color: #E7E7E7;
    cursor: pointer;
    border: 1px solid #E7E7E7;
}

.helper-error{
    margin: 5px 0 0 0 !important;
    color: red;
    min-height: 18px;
}

.invalid{
    color: red;
    border-bottom: 1px solid red !important;
}

.invalid::-webkit-input-placeholder {
    color: red;
    opacity: 1; /* Firefox */
}

.invalid:-ms-input-placeholder {
    color: red;
    opacity: 1; /* Firefox */
}

.invalid::-ms-input-placeholder {
    color: red;
    opacity: 1; /* Firefox */
}

.invalid::placeholder {
    color: red;
    opacity: 1; /* Firefox */
}


@media only screen and (max-width: 850px) {

    .login-form{
        width: 70%;
    }

    .login-form input{
        font-size: 10pt;
    }

    .login-form-btn{
        font-size: 10pt;
    }

    .helper-error{
        font-size: 10pt;
    }

}




