.footer{
    width: 100%;
}

.footer-top{
    width: 100%;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-align: center;
        align-items: center;
}

.footer-line{
    border: 1px solid #000;
    width: calc( (33.33% - 100px)/2 );
}

.footer-title{
    width: 200px;
    text-align: center;
    font-size: 13pt;
    background-color: #000;
    color: #fff;
    padding: 5px;
}
.footer-content{
    background-color: #E7E7E7;
    display: -ms-flexbox;
    display: flex;
    min-height: 60px;
}

.about-footer,
.contact-footer,
.social-footer{
    width: 33.33%;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: center;
        justify-content: center;
    -ms-flex-align: center;
        align-items: center;
    padding: 20px;
}

.about-footer{
    text-align: justify;
}

.contact-footer,
.social-footer {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;
}

.link-item{
    display: -ms-flexbox;
    display: flex;
    -ms-flex-align: center;
        align-items: center;
    margin-top: 5px;
    width: 250px;
}


.footer-icon{
    border-radius: 50%;
    background-color: #000;
    width: 30px;
    height: 30px;
    background-position: center;
    background-size: 60%;
    background-repeat: no-repeat;
    margin-right: 10px;
}

.linkedin-icon{
    background-image: url("../../icons/linkedin_white.png");
}

.mail-icon{
    background-image: url("../../icons/envelope.png");
}

.footer-link,
.footer-link:visited,
.footer-link:hover,
.footer-link:active{
    text-decoration: none;
    color: #000;
}

.facebook-icon{
    background-image: url("../../icons/facebook-logo.png");
}

.instagram-icon{
    background-image: url("../../icons/instagram-logo.png");
}

.mobile-footer-title {
    display: none;
}

@media only screen and (max-width: 850px) {

    .show-on-large-screen{
        display: none;
    }

    .footer-content{
        -ms-flex-direction: column;
            flex-direction: column;
        -ms-flex-align: center;
            align-items: center;
    }

    .mobile-footer-title{
        width: 100%;
        display: -ms-flexbox;
        display: flex;
        -ms-flex-align: center;
            align-items: center;
        margin: 10px 0;
    }

    .footer-line{
        width: calc( (100% - 100px)/2 );
    }

    .about-footer,
    .contact-footer,
    .social-footer{
        width: 90%;
        padding: 0px;
    }

    .social-footer {
        margin-bottom: 10px;
    }

}


