.list-books,
.bookshelf{
    width: 100%;
}

.list-books{
    padding-top: 30px;
    -ms-flex-positive: 1;
        flex-grow: 1;
}

.bookshelf-header{
    width: 100%;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: stretch;
        justify-content: stretch;
    -ms-flex-align: center;
        align-items: center;
}

.left-line ,
.right-line {
    border: 1px solid #000;
    width: calc(50% - 75px);

}

.bookshelf-title{
    width: 150px;
    text-align: center;
    font-size: 13pt;
    background-color: #000;
    color: #fff;
    padding: 5px;
}

.bookshelf-books{
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: center;
        justify-content: center;
    -ms-flex-wrap: wrap;
        flex-wrap: wrap;
    padding: 20px;
}

.login-alert-home{
    text-align: center;
    margin-top: 50px !important;
    font-size: 16pt;
}

@media only screen and (max-width: 850px) {

    .login-alert-home{
        font-size: 12pt;
    }
}


